@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');


body{
  margin: 0;
  padding: 0;
  font-size: .95rem;
  font-family: 'Roboto',sans-serif;

}


button:hover{
  cursor: pointer;
}

/* Navbar styles */

.navbar{
  width: 100%;
  padding: 1em 0;
  display: flex;
  justify-content: space-around;
  background: #757050;

}

.nav-div{
  display: flex;
  align-items: center;
  color: white;
}



.link{
  text-decoration: none;
}

/*#############################################*/


/* Homepage styles */
.quiz-options{
  width: 75%;
  margin: 3em auto;
  text-align: center;
}

.quiz-card{
  margin: 3em auto;
  padding: 1em;
  border: 1px solid grey;
  border-radius: 5px;
}

.quizCard-button{
  background:  #757050;
  border: 1px solid grey;
  color: white;
  padding: 1em;
  border-radius: 5px;
}



/*#############################################*/


/* QuizPage styles */
.quiz-wrapper{
  margin: 3em auto;
  width: 85%;
}

.quiz-header{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.timer-container{
  text-align: left;
  font-weight: bolder;
}

.option-div{
  display: flex;
  flex-direction: column;

}

.option{
  padding: .55em;
  margin: .55em 0;
}

.score{
  width: 55%;
  margin: 0 auto;
  text-align: center;
}

.score-h{
  font-size: 1.5rem;
}

.answer-div-button{
  font-size: 1.3rem;
  font-weight: bolder;
  width: 100%;
}

.answer-div-button div{
  margin: .55em 0;
  width: 100%;
}

.icons{
  margin-right: .55em;
}

.answer-div{
  width:90%;
  margin: 1em auto;

}
.answer-labels{
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.answer-item{
  display: flex;
  width: 100%;
  margin: 1em auto;
  justify-content: space-between;
  font-size: 1rem;
  padding-bottom: .75em;
  border-bottom: 1px dashed grey;
}

.pagination-div{
  width:90%;
  margin: 1em auto;
  display: flex;
  justify-content: space-around;

}

.pagination-item{
  background:#757050;
  border-radius: 5px;
  text-align: center;
  padding: .1em 1.5em;
  font-size: .55rem;
}

.pagination-item p{
  color: white;
  font-weight: bolder;
}

.loading{
  text-align: center;
}

.pagination-item-active{
  background: white;
  color:#757050;
  border-radius: 5px;
  text-align: center;
  padding: .1em 1.5em;
  font-size: .55rem;
  border: 1px solid #757050;
}

.answers-question-div,.answers-answer-div{
  width:45%;
  font-size: .75rem;
  word-break: break-all;

}

.answers-answer-div{
  text-align: center;
}
/* ####################################### */


/* About page */

.about-div{
  width: 90%;
  margin: 2em auto;
  font-size: 1.3rem;
}

.form-body{
  resize: none;
  height: 200px;
  width: 75%;
  font-family: 'Roboto';
  border: 2px solid black;


}

.about-div input{
  font-family: 'Roboto';
  margin: .55em 0;
  width: 75%;
  border: 2px solid black;
}

.about-form{
  width: 100%;
  border: 2px solid black;
  text-align: center;
  background: #757050;
  color: white;
}


.submit{
  display: block;
  margin: 1em auto;
  width: 75%;
  padding: .55em;
}

.footer{
  width: 100%;
  text-align: center;
  font-size: 1.3rem;
  border-top: 1px dashed #757050;

}

.footer h2{
  color:#757050;
}



/* ########################################## */


/* Big screen */

@media (min-width:900px){
  .quiz-options{
    display: flex;
    width: 75%;
    margin: 3em auto;
    justify-content: space-around;


  }

  .quiz-options div{
    width: 33%;
    margin: 0 1em;
  }

  .footer{
    width: 100%;
    text-align: center;
    font-size: 1.3rem;
    border-top: 1px dashed #757050;


  }


  .footer h2{
    color:#757050;
  }

  .quiz-wrapper{
    width:55%;
  }

  .answer-div-button{
    width: 100%;
    margin: 5em 0;
    display: flex;
    justify-content: space-between;
  }

  .answer-div-button div{
    text-align: center;
  }

  .answer-div-button div:hover{
    cursor: pointer;
    font-size: 1.6rem;
  }

}
